import React, { useState } from 'react'
import { Button, Popover, Upload } from 'antd'
import { CirclePicker } from 'react-color'
import style from './style.module.scss'

const { Dragger } = Upload

const CoverPopup = props => {
  const { children } = props

  const [color, setColor] = useState('#141322')
  const [visible, setVisible] = useState(false)

  const handleColorChange = v => {
    setColor(v.hex)
  }

  const handleSubmit = () => {
    setVisible(false)
  }

  const handleVisibleChange = v => {
    setVisible(v)
  }

  return (
    <Popover
      overlayClassName={style.popup}
      title="Add New Label"
      visible={visible}
      trigger="click"
      placement="bottomRight"
      getPopupContainer={() => {
        return document.querySelector('.dialog-sidebar')
      }}
      onVisibleChange={handleVisibleChange}
      content={
        <div>
          <Dragger
            style={{ borderRadius: '6px 6px 0 0', border: 'none', backgroundColor: '#F4F7FC' }}
          >
            <p>
              <i className="fe fe-image" style={{ fontSize: '20px' }} />
            </p>
            <p>Add Cover</p>
          </Dragger>
          <div style={{ padding: '12px 16px' }}>
            <div className="field-label">Or select by color</div>
            <CirclePicker
              color={color}
              onChange={handleColorChange}
              width={300}
              circleSize={40}
              circleSpacing={10}
              colors={[
                '#51C3C4',
                '#584ABE',
                '#F1C40F',
                '#E84393',
                '#F2994A',
                '#E74C3C',
                '#1358AA',
                '#27AE60',
                '#DFF3E7',
                '#F7A7CE',
                '#A1A1C2',
                '#141322',
              ]}
            />
            <div className="actions text-right mt-4">
              <Button onClick={handleSubmit} type="text">
                Remove
              </Button>
              <Button onClick={handleSubmit} type="primary">
                Save
              </Button>
            </div>
          </div>
        </div>
      }
    >
      {children}
    </Popover>
  )
}

export default CoverPopup
