import React, { useState, useEffect } from 'react'
import { useLocation, useParams } from 'react-router'
import { connect } from 'react-redux'
import WorkspaceList from './list'
import WorkspaceKanban from './kanban'
import WorkspaceForm from './form'

const mapStateToProps = ({ settings, workspace, dispatch }) => ({
  dispatch,
  list: workspace.list,
  loading: workspace.loading,
  workspaceMenuWidth: settings.workspaceMenuWidth,
})

const useQuery = () => {
  return new URLSearchParams(useLocation().search)
}

const Workspace = ({ list, dispatch }) => {
  const query = useQuery()
  const view = query.get('view') || 'kanban'
  const params = useParams()
  const [guid, setGuid] = useState()

  useEffect(() => {
    dispatch({
      type: 'workspace/LOAD_WORKSPACE',
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    if (params.guid) {
      setGuid(params.guid)
    }
  }, [params.guid])

  const handleKanbanItemClick = item => {
    window.history.pushState(
      null,
      null,
      `/?cover=${item.cover ? 'yes' : 'no'}#workspace/${item.guid}`,
    )
    setGuid(item.guid)
  }

  const handleFormClose = () => {
    window.history.pushState(null, null, `/#/workspace`)
    setGuid(undefined)
  }

  return (
    <div>
      <div className="row">
        <div className="col-lg-12">
          {view === 'kanban' ? (
            <WorkspaceKanban onItemClick={handleKanbanItemClick} />
          ) : (
            <WorkspaceList list={list} />
          )}
          {guid ? <WorkspaceForm guid={guid} onClose={handleFormClose} /> : null}
        </div>
      </div>
    </div>
  )
}

export default connect(mapStateToProps)(Workspace)
