import React from 'react'
import { Link } from 'react-router-dom'
import { useLocation } from 'react-router'
import { Button } from 'antd'
import { useDispatch } from 'react-redux'
import NProgress from 'nprogress'
import { ReloadOutlined } from '@ant-design/icons'
import style from './style.module.scss'

const WorkspaceSwitcher = () => {
  const dispatch = useDispatch()

  const useQuery = () => {
    return new URLSearchParams(useLocation().search)
  }

  const query = useQuery()
  const view = query.get('view') || 'kanban'

  const reload = async () => {
    NProgress.start()
    await dispatch({
      type: 'workspace/LOAD_WORKSPACE',
    })
    NProgress.done()
  }

  return (
    <div>
      <Link to="?view=kanban">
        <Button
          className={`${style['btn-menu']} ${view === 'kanban' ? style.active : ''}`}
          type="link"
          size="large"
          icon={<i className="icmn-trello" />}
        />
      </Link>
      <Link to="?view=list">
        <Button
          className={`${style['btn-menu']} ${view === 'list' ? style.active : ''}`}
          type="link"
          size="large"
          icon={<i className="icmn-list" />}
        />
      </Link>

      <Button
        onClick={reload}
        className="ml-3"
        type="primary"
        size="large"
        icon={<ReloadOutlined />}
      />
    </div>
  )
}

export default WorkspaceSwitcher
