import React, { useState } from 'react'
import { Avatar, Button, Col, Input, Popover, Row } from 'antd'
import { SearchOutlined } from '@ant-design/icons'
import style from './style.module.scss'

const MemberPopup = props => {
  const { children } = props

  const [visible, setVisible] = useState(false)

  const handleSubmit = () => {
    setVisible(false)
  }

  const handleVisibleChange = v => {
    setVisible(v)
  }

  const members = [
    {
      key: 1,
      avatar: '/resources/images/avatars/1.jpg',
      checked: true,
    },
    {
      key: 2,
      avatar: '/resources/images/avatars/2.jpg',
      checked: true,
    },
    {
      key: 3,
      avatar: '/resources/images/avatars/3.jpg',
      checked: true,
    },
    {
      key: 4,
      avatar: '/resources/images/avatars/4.jpg',
    },
    {
      key: 5,
      initial: 'TS',
      accent: '#F2994A',
    },
    {
      key: 6,
      avatar: '/resources/images/avatars/5.jpg',
    },
    {
      key: 7,
      avatar: '/resources/images/avatars/1.jpg',
    },
    {
      key: 8,
      avatar: '/resources/images/avatars/2.jpg',
    },
    {
      key: 9,
      avatar: '/resources/images/avatars/3.jpg',
    },
    {
      key: 10,
      avatar: '/resources/images/avatars/4.jpg',
    },
    {
      key: 11,
      initial: 'TS',
      accent: '#E84393',
    },
    {
      key: 12,
      avatar: '/resources/images/avatars/5.jpg',
    },
  ]

  return (
    <Popover
      overlayClassName={style.popup}
      title="Add New Member"
      visible={visible}
      trigger="click"
      placement="bottomLeft"
      getPopupContainer={() => {
        return document.querySelector('.dialog-sidebar')
      }}
      onVisibleChange={handleVisibleChange}
      content={
        <div>
          <Input className="mb-4" placeholder="Search member" prefix={<SearchOutlined />} />
          <Row>
            {members.map(m => {
              return (
                <Col key={m.key} lg={4}>
                  {m.avatar ? (
                    <Avatar className={m.checked ? 'checked' : ''} size={40} src={m.avatar} />
                  ) : (
                    <Avatar
                      className={m.checked ? 'checked' : ''}
                      size={40}
                      style={{ backgroundColor: m.accent, color: '#ffffff' }}
                    >
                      {m.initial}
                    </Avatar>
                  )}
                </Col>
              )
            })}
          </Row>
          <div className="actions text-right mt-4">
            <Button onClick={handleSubmit} type="primary">
              Save
            </Button>
          </div>
        </div>
      }
    >
      {children}
    </Popover>
  )
}

export default MemberPopup
