import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  Table,
  // Button,
  Input,
  Form,
  Select,
  // Tabs
} from 'antd'
import { Badge, Button as BootstrapButton } from 'reactstrap'
import { timeFormat } from 'd3-time-format'
import {
  // PlusOutlined,
  // DownloadOutlined,
  InfoCircleFilled,
  CaretDownOutlined,
} from '@ant-design/icons'
import WorkspaceSwitcher from 'components/cleanui/system/Workspace/Switcher'
import style from './style.module.scss'

// const { TabPane } = Tabs

const badgeColors = {
  open: 'info',
  'in progress': 'warning',
  done: 'success',
}

const HelpdeskList = ({ list }) => {
  const dispatch = useDispatch()
  const { searchConfig, total } = useSelector(state => state.helpdesk)
  const { filters: dataFilters = [] } = searchConfig
  const getTitleMap = key => {
    const titleMap = {
      ticket_number: 'Ticket Number',
      status_name: 'Status',
      status__name: 'Status',
      name: 'Title',
      created_by_full_name: 'Requestor',
      receive_time: 'Receive Time',
      response_time: 'Response Time',
      resolution_time: 'Resolution Time',
    }
    return titleMap[key] || key
  }
  const handleSearch = (filters, dataIndex, confirm) => {
    const filterId = dataFilters.findIndex(filter => filter.field === dataIndex)
    const newDataFilters = [...dataFilters]
    const currentFilter = {
      ...filters[0],
      field: dataIndex,
      sort: filters[0].sort || 'none',
      op: filters[0].op || 'contains',
    }
    const filterExist = filterId >= 0
    newDataFilters.splice(filterExist ? filterId : 0, filterExist ? 1 : 0, currentFilter)
    dispatch({
      type: 'helpdesk/SEARCH_HELPDESK',
      payload: {
        filters: newDataFilters,
        start: 1,
      },
    })
    confirm()
  }
  const onChangePerPage = value => {
    dispatch({
      type: 'helpdesk/SEARCH_HELPDESK',
      payload: {
        limit: Number(value),
        start: 1,
      },
    })
  }
  const onPaginationChange = page => {
    dispatch({
      type: 'helpdesk/SEARCH_HELPDESK',
      payload: {
        start: Number(page),
      },
    })
  }
  const getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys: setFilters, selectedKeys: filters, confirm }) => {
      const setFilterItems = (key, value) => {
        const newFilters = [...filters]
        newFilters[0] = {
          ...newFilters[0],
          [key]: value,
        }
        setFilters(newFilters)
      }
      return (
        <Form layout="vertical" className="p-4">
          <Form.Item label={`Search By ${getTitleMap(dataIndex)}`} name="value">
            <Input
              placeholder="Type here..."
              value={(filters[0] || {}).value}
              onChange={e => setFilterItems('value', e.target.value)}
            />
          </Form.Item>
          <Form.Item label="Sort Order" name="sort">
            <Select defaultValue="none" onChange={value => setFilterItems('sort', value)}>
              <Select.Option value="none">None</Select.Option>
              <Select.Option value="asc">Ascending</Select.Option>
              <Select.Option value="desc">Descending</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="Filter Column" name="op">
            <Select defaultValue="contains" onChange={value => setFilterItems('op', value)}>
              <Select.Option value="contains">Contains</Select.Option>
              <Select.Option value="like">Like</Select.Option>
            </Select>
          </Form.Item>
          <BootstrapButton
            onClick={() => handleSearch(filters, dataIndex, confirm)}
            color="warning"
            className="w-100"
          >
            <i className="fe fe-search" />
            Search
          </BootstrapButton>
        </Form>
      )
    },
    filterIcon: filtered => (
      <CaretDownOutlined style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: () => true,
  })
  const tableColumns = [
    {
      title: '#',
      dataIndex: 'info',
      key: 'info',
      render: () => <InfoCircleFilled className="text-warning" />,
    },
    {
      title: getTitleMap('ticket_number'),
      dataIndex: 'ticket_number',
      key: 'ticket_number',
      ...getColumnSearchProps('ticket_number'),
    },
    {
      title: getTitleMap('status_name'),
      dataIndex: 'status_name',
      key: 'status_name',
      render: status => (
        <Badge className="font-size-12" pill color={badgeColors[(status || '').toLowerCase()]}>
          {status}
        </Badge>
      ),
      ...getColumnSearchProps('status__name'),
    },
    {
      title: getTitleMap('name'),
      dataIndex: 'name',
      key: 'name',
      ...getColumnSearchProps('name'),
    },
    {
      title: getTitleMap('created_by_full_name'),
      dataIndex: 'created_by_full_name',
      key: 'created_by_full_name',
      ...getColumnSearchProps('created_by_full_name'),
    },
    {
      title: getTitleMap('receive_time'),
      dataIndex: 'receive_time',
      key: 'receive_time',
      render: time => time && timeFormat('%Y-%m-%d %H:%M:%S')(new Date(time)),
      ...getColumnSearchProps('receive_time'),
    },
    {
      title: getTitleMap('response_time'),
      dataIndex: 'response_time',
      key: 'response_time',
      render: time => time && timeFormat('%Y-%m-%d %H:%M:%S')(new Date(time)),
      ...getColumnSearchProps('response_time'),
    },
    {
      title: getTitleMap('resolution_time'),
      dataIndex: 'resolution_time',
      key: 'resolution_time',
      render: time => time && timeFormat('%Y-%m-%d %H:%M:%S')(new Date(time)),
      ...getColumnSearchProps('resolution_time'),
    },
  ]
  const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
      console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
    },
    getCheckboxProps: record => ({
      disabled: record.name === 'Disabled User', // Column configuration not to be checked
      name: record.name,
    }),
  }
  return (
    <div>
      <div className="d-flex justify-content-between mb-4">
        {/* <Tabs className={style['contain-tabs']}>
          <TabPane key="1" tab={<Button type="primary" size="large" icon={<PlusOutlined />}>Create New</Button>} />
          <TabPane key="2" tab={<Button size="large" icon={<DownloadOutlined />} className="ml-3">Download</Button>} />
          <TabPane key="3" tab={<Button size="large" icon={<DownloadOutlined />} className="ml-3">Download</Button>} />
          <TabPane key="4" tab={<Button size="large" icon={<DownloadOutlined />} className="ml-3">Download</Button>} />
          <TabPane key="5" tab={<Button size="large" icon={<DownloadOutlined />} className="ml-3">Download</Button>} />
          <TabPane key="6" tab={<Button size="large" icon={<DownloadOutlined />} className="ml-3">Download</Button>} />
          <TabPane key="7" tab={<Button size="large" icon={<DownloadOutlined />} className="ml-3">Download</Button>} />
        </Tabs> */}
        <div className={style['contain-views']}>
          <WorkspaceSwitcher />
        </div>
      </div>
      <div className="kit__utils__table">
        <div className={`card ${style['contain-table']}`}>
          <Table
            rowSelection={rowSelection}
            columns={tableColumns}
            dataSource={list}
            pagination={{
              defaultPageSize: 10,
              defaultCurrent: 1,
              pageSize: searchConfig.limit,
              current: searchConfig.start,
              total,
              onChange: onPaginationChange,
            }}
          />
          {total > 0 ? (
            <div className={style['per-page']}>
              <span>Display </span>
              <Select defaultValue={searchConfig.limit || 10} onChange={onChangePerPage}>
                <Select.Option value="5">5</Select.Option>
                <Select.Option value="10">10</Select.Option>
                <Select.Option value="20">20</Select.Option>
                <Select.Option value="30">30</Select.Option>
              </Select>
              <span>of {total} rows</span>
            </div>
          ) : null}
        </div>
      </div>
    </div>
  )
}

export default HelpdeskList
