import React, { useState, useEffect, useCallback, useRef } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { Button, Drawer, Dropdown, Input, Menu } from 'antd'
import debounce from 'lodash.debounce'

import { DownOutlined, SearchOutlined } from '@ant-design/icons'
import { WorkspaceSwitcher, WorkspacePanel, KanbanItem } from 'components/cleanui/system/Workspace'
import usePrevious from 'hooks/usePrevious'
import WorkspaceMenu from '../menu'
import style from './style.module.scss'

const WorkspaceKanban = props => {
  const { onItemClick = () => {} } = props

  const { list: sourceList, total, loading, limit } = useSelector(state => state.workspace)
  const { workspaceMenuWidth } = useSelector(state => state.settings)
  const infiniteLoader = useRef(null)
  const dispatch = useDispatch()
  const previousList = usePrevious(sourceList) || []
  const [page, setPage] = useState(1)
  const [totalPage, setTotalPage] = useState(1)
  const [list, setList] = useState([])

  const filterByStatus = useCallback(
    statusName => {
      const filteredList = [...list].filter(data => {
        if (statusName === 'Backlog') {
          return data.status === null
        }
        return data.status_name === statusName
      })

      return filteredList.length > 0
        ? filteredList.map(data => {
            return (
              <KanbanItem
                item={data}
                key={data.guid}
                onClick={item => {
                  onItemClick(item)
                }}
              />
            )
          })
        : []
    },
    [list, onItemClick],
  )

  const [backlogItems, setBacklogItems] = useState([])
  const [todoItems, setTodoItems] = useState([])
  const [inprogressItems, setInprogressItems] = useState([])
  const [completed, setCompleted] = useState([])

  const isArrayOfObjectEqual = useCallback(
    (array1, array2) =>
      array1.length === array2.length &&
      array1.every((element, index) => {
        return JSON.stringify(element) === JSON.stringify(array2[index])
      }),
    [],
  )

  const handleObserver = useCallback(
    debounce(entities => {
      const target = entities[0]
      if (target.isIntersecting && page < totalPage && !loading) {
        setPage(page + 1)
      }
    }, 600),
    [page, totalPage, loading],
  )

  const [isMenuOpen, setIsMenuOpen] = useState(false)

  const toggleMenuOpen = () => {
    setIsMenuOpen(!isMenuOpen)
  }

  useEffect(() => {
    setBacklogItems(filterByStatus('Backlog'))
    setTodoItems(filterByStatus('Open'))
    setInprogressItems(filterByStatus('In Progress'))
    setCompleted(filterByStatus('Done'))
  }, [list, filterByStatus])

  useEffect(() => {
    if (!isArrayOfObjectEqual(sourceList, previousList)) {
      setList(l => [...l, ...sourceList])
    }
  }, [sourceList, previousList, isArrayOfObjectEqual])

  useEffect(() => {
    if (page > 1) {
      dispatch({
        type: 'workspace/SEARCH_WORKSPACE',
        payload: {
          start: Number(page),
        },
      })
    }
  }, [page, dispatch])

  useEffect(() => {
    const options = {
      root: null,
      rootMargin: '20px',
      threshold: 1.0,
    }

    const observer = new IntersectionObserver(handleObserver, options)
    if (infiniteLoader.current) {
      observer.observe(infiniteLoader.current)
    }
  }, [handleObserver])

  useEffect(() => {
    setTotalPage(Math.round(total / limit))
  }, [total, limit])

  return (
    <div className={loading ? style.loading : ''}>
      <div className="d-flex justify-content-between mb-3">
        <div className={style['contain-actions']}>
          <Button
            onClick={toggleMenuOpen}
            type="primary"
            size="large"
            icon={
              <i
                className="fe fe-book-open"
                style={{ marginRight: 5, position: 'relative', top: 2 }}
              />
            }
          >
            Workspace
          </Button>
        </div>
        <div className={style['contain-title']}>
          <span
            style={{ fontSize: '20px', color: '#141322', lineHeight: '40px', fontWeight: 'bold' }}
          >
            Workspace Name
          </span>
        </div>
        <div className={style['contain-search']}>
          <Input
            size="large"
            prefix={<SearchOutlined style={{ marginRight: 12, color: '#1358AA' }} />}
            placeholder="Search everything"
          />
        </div>
        <div className={style['contain-groups']}>
          <Dropdown overlay={<Menu />}>
            <Button className="mr-4" size="large">
              View by Status <DownOutlined style={{ fontSize: '12px' }} />
            </Button>
          </Dropdown>
        </div>
        <div className={style['contain-views']}>
          <WorkspaceSwitcher />
        </div>
      </div>
      <div className={`row ${style['kanban-row']}`}>
        <div className="col-lg-3 d-flex">
          <WorkspacePanel title="Backlog" items={backlogItems} />
        </div>
        <div className="col-lg-3 d-flex">
          <WorkspacePanel title="To Do" items={todoItems} />
        </div>
        <div className="col-lg-3 d-flex">
          <WorkspacePanel title="In Progress" items={inprogressItems} />
        </div>
        <div className="col-lg-3 d-flex">
          <WorkspacePanel title="Completed" items={completed} />
        </div>
        <div className="col-add d-flex">
          <WorkspacePanel.Create />
        </div>
      </div>
      <Drawer
        className={style.workspaceDrawer}
        closable={false}
        visible={isMenuOpen}
        placement="left"
        onClose={toggleMenuOpen}
        maskClosable
        getContainer={null}
        width={workspaceMenuWidth}
      >
        <WorkspaceMenu />
      </Drawer>
    </div>
  )
}

export default WorkspaceKanban
