import React, { useState } from 'react'
import { Button, Input, Popover, Upload } from 'antd'
import { CloudUploadOutlined } from '@ant-design/icons'
import style from './style.module.scss'

const { Dragger } = Upload

const FilePopup = props => {
  const { children } = props

  const [visible, setVisible] = useState(false)

  const handleSubmit = () => {
    setVisible(false)
  }

  const handleVisibleChange = v => {
    setVisible(v)
  }

  return (
    <Popover
      overlayClassName={style.popup}
      title="Add Attachment"
      visible={visible}
      trigger="click"
      placement="bottomLeft"
      getPopupContainer={() => {
        return document.querySelector('.dialog-sidebar')
      }}
      onVisibleChange={handleVisibleChange}
      content={
        <div>
          <Dragger style={{ borderRadius: 6, backgroundColor: '#F4F7FC' }}>
            <p>
              <CloudUploadOutlined style={{ fontSize: '20px' }} />
            </p>
            <p>Browse file or drag here</p>
          </Dragger>
          <div className="field-label">Or attach a link</div>
          <Input className="mb-2" placeholder="Enter a link" />
          <div className="actions text-right mt-4">
            <Button onClick={handleSubmit} type="primary">
              Attach
            </Button>
          </div>
        </div>
      }
    >
      {children}
    </Popover>
  )
}

export default FilePopup
