import React, { useState, useEffect, useMemo } from 'react'
import { Tabs } from 'antd'
import style from './style.module.scss'

const { TabPane } = Tabs

const Pane = () => {}

const Section = props => {
  const { title, actions, defaultActiveKey, children, bordered = false } = props

  const [activePane, setActivePane] = useState(defaultActiveKey)

  const handleTabChange = key => {
    setActivePane(key)
  }

  const count = React.Children.count(children)

  const panes = useMemo(() => {
    const items = []
    React.Children.forEach(children, (c, i) => {
      const attrs = {
        key: c.key || `pane-${i}`,
      }
      const p = React.cloneElement(c, attrs)
      items.push(p)
    })
    return items
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [count])

  useEffect(() => {
    if (!activePane && panes.length) {
      const active = panes[0].key
      setActivePane(active)
    }
  }, [activePane, panes])

  return (
    <div className={`card ${style.section}`}>
      <div className={`card-header card-header-flex ${bordered ? '' : 'no-border'}`}>
        <div className="d-flex flex-column justify-content-center mr-auto">
          <h5 className={style['section-header']}>{title}</h5>
        </div>
        <Tabs onChange={handleTabChange} activeKey={activePane} className="kit-tabs-pills">
          {panes.map(p => {
            const { tab = null } = p.props
            return <TabPane key={p.key} tab={tab} />
          })}
        </Tabs>
        <div className="actions d-inline-flex align-items-center">{actions}</div>
      </div>
      <div className="card-body">
        {panes.map(p => {
          return (
            <div hidden={p.key !== activePane} key={p.key}>
              {p.props.children}
            </div>
          )
        })}
      </div>
    </div>
  )
}

Section.Pane = Pane

export default Section
