import React, { useState } from 'react'
import { Button, Col, Input, Popover, Row } from 'antd'
import style from './style.module.scss'

const DatePopup = props => {
  const { children } = props

  const [visible, setVisible] = useState(false)

  const handleSubmit = () => {
    setVisible(false)
  }

  const handleVisibleChange = v => {
    setVisible(v)
  }

  return (
    <Popover
      overlayClassName={style.popup}
      title="Add Due Date"
      visible={visible}
      trigger="click"
      placement="bottomLeft"
      getPopupContainer={() => {
        return document.querySelector('.dialog-sidebar')
      }}
      onVisibleChange={handleVisibleChange}
      content={
        <div>
          <Row gutter={8} className="mb-2">
            <Col lg={12}>
              <span className="field-label">Date</span>
            </Col>
            <Col lg={12}>
              <span className="field-label">Time</span>
            </Col>
          </Row>
          <Row gutter={8}>
            <Col lg={12}>
              <Input placeholder="DD/MM/YYYY" />
            </Col>
            <Col lg={12}>
              <Input placeholder="12:00 AM" />
            </Col>
          </Row>
          <div className="actions text-right mt-4">
            <Button onClick={handleSubmit} type="text">
              Remove
            </Button>
            <Button onClick={handleSubmit} type="primary">
              Save
            </Button>
          </div>
        </div>
      }
    >
      {children}
    </Popover>
  )
}

export default DatePopup
