import React, { useMemo } from 'react'
import { Avatar, Button } from 'antd'
// import { PaperClipOutlined } from '@ant-design/icons'
// import { timeFormat } from 'd3-time-format'
import { shuffle } from 'utils/array'
import style from './style.module.scss'

const KanbanItem = props => {
  const { item, onClick = () => {} } = props
  const { key, name } = item

  const lineColors = shuffle(['#27AE60', '#F1C40F', '#E84393', '#1358AA', '#51C3C4', '#F2994A'])
  // const backgrounds = shuffle(['#9B51E0', '#F1C40F', '#219653'])
  const daysLeft = useMemo(() => shuffle([true, false]), [])
  const attachment = useMemo(() => shuffle([true, false]), [])
  const labels = useMemo(() => shuffle([false, false, true, false]), [])
  const covers = useMemo(() => shuffle(['3.jpeg', '', '', '', '', '', '', '4.jpeg']), [])
  const images = useMemo(() => shuffle(['2.jpeg', '', '', '', '', '', '', '6.jpeg']), [])
  const assignes = useMemo(() => shuffle([true, true, false]), [])

  const handleClick = () => {
    const copy = { ...item, cover: !!covers[0] }
    onClick(copy)
  }

  return (
    <div
      className={`${covers[0] ? style['card-covered'] : style.card} ${style.cardItem}`}
      key={key}
      aria-hidden="true"
      onClick={handleClick}
      style={
        covers[0] ? { backgroundImage: `url(resources/images/content/photos/${covers[0]})` } : {}
      }
    >
      <div className={covers[0] ? style['content-covered'] : style.content}>
        {images[0] && !covers[0] ? (
          <img
            className="rounded img-fluid mb-2"
            src={`resources/images/content/photos/${images[0]}`}
            alt="Upload"
          />
        ) : null}
        <div className="d-flex flex-wrap-reverse align-items-center justify-content-between">
          <div>
            {labels[0] ? (
              <React.Fragment>
                <span
                  className={`${style['flag-label']} d-inline-block`}
                  style={{ backgroundColor: lineColors[0] }}
                >
                  TEXT
                </span>
                <span
                  className={`${style['flag-label']} d-inline-block`}
                  style={{ backgroundColor: lineColors[1] }}
                >
                  TEXT
                </span>
              </React.Fragment>
            ) : (
              <React.Fragment>
                <span
                  className={`${style.flag} d-inline-block`}
                  style={{ backgroundColor: lineColors[0] }}
                >
                  &nbsp;
                </span>
                <span
                  className={`${style.flag} d-inline-block`}
                  style={{ backgroundColor: lineColors[1] }}
                >
                  &nbsp;
                </span>
              </React.Fragment>
            )}
          </div>
          {daysLeft[0] ? (
            <span className="font-size-12 font-weight-bold text-danger">2 days left</span>
          ) : null}
        </div>
        <div className="d-flex flex-wrap-reverse align-items-center">
          <h5
            className="text-dark font-weight-bold font-size-14 mt-1 mb-1 mr-auto"
            style={{ textTransform: 'capitalize' }}
          >
            {name}
          </h5>
        </div>
        <div className="d-flex align-items-center flex-wrap pt-2 justify-content-between">
          {assignes[0] ? (
            <div className="d-flex">
              <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0">
                <Avatar size={24} src="resources/images/avatars/1.jpg" />
              </div>
              <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0">
                <Avatar size={24} src="resources/images/avatars/2.jpg" />
              </div>
              <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0">
                <Avatar size={24} src="resources/images/avatars/3.jpg" />
              </div>
              <div className="kit__utils__avatar kit__utils__avatar--rounded kit__utils__avatar--size27 flex-shrink-0">
                <Avatar
                  size={24}
                  style={{
                    color: '#000000',
                    backgroundColor: '#F3F6FC',
                    fontSize: 10,
                    fontWeight: 600,
                  }}
                >
                  +4
                </Avatar>
              </div>
            </div>
          ) : null}
          <div className={style.badges}>
            {attachment[0] ? (
              <Button
                size="small"
                type="text"
                icon={<i className="fe fe-paperclip" style={{ marginRight: 5 }} />}
              >
                2
              </Button>
            ) : null}
            {attachment[0] ? (
              <Button
                size="small"
                type="text"
                icon={<i className="fe fe-check-square" style={{ marginRight: 5 }} />}
              >
                0/5
              </Button>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  )
}

export default KanbanItem
