import React, { useState } from 'react'
import { Button, Checkbox, Col, Input, Progress, Row } from 'antd'
import style from './style.module.scss'

const { TextArea } = Input

const TodoEditor = props => {
  const { items } = props

  const [editing, setEditing] = useState()

  const handleCheckClick = item => {
    setEditing(item.key)
  }

  const renderTitle = item => {
    return item.checked ? <del>{item.title}</del> : item.title
  }

  const handleCancelAction = () => {
    setEditing(null)
  }

  const handelSaveAction = () => {
    setEditing(null)
  }

  return (
    <div className={style['todo-editor']}>
      <div className="d-flex">
        <div
          className="text-gray-6 pr-3"
          style={{ width: 40, fontSize: 11, position: 'relative', top: 2 }}
        >
          35%
        </div>
        <Progress style={{ flex: 1 }} percent={35} size="small" status="success" showInfo={false} />
      </div>
      <div>
        {items.map(item => {
          return (
            <div key={item.key} className="my-3 todo-item">
              <div className="d-flex">
                <div style={{ width: 40 }}>
                  <Checkbox defaultChecked={item.checked} />
                </div>
                <div style={{ flex: 1 }}>
                  {editing === item.key ? (
                    <React.Fragment>
                      <TextArea defaultValue={item.title} autoFocus />
                      <Row className="my-2">
                        <Col lg={12} className="toolbar" />
                        <Col lg={12} className="actions text-right">
                          <Button onClick={handleCancelAction} type="text" primary>
                            Cancel
                          </Button>
                          <Button onClick={handelSaveAction} type="primary">
                            Save
                          </Button>
                        </Col>
                      </Row>
                    </React.Fragment>
                  ) : (
                    <div className="todo-title" onClick={() => handleCheckClick(item)} aria-hidden>
                      {renderTitle(item)}
                    </div>
                  )}
                </div>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default TodoEditor
