import React, { useState, useEffect } from 'react'
// import { ReactSortable } from 'react-sortablejs'
import { Button, Dropdown, Menu } from 'antd'
import PerfectScrollbar from 'react-perfect-scrollbar'
import style from './style.module.scss'

const WorkspacePanel = props => {
  const { title, items: defaultItems = [] } = props

  const [items, setItems] = useState(defaultItems)

  useEffect(() => {
    setItems(defaultItems)
  }, [defaultItems])

  const dropdownMenu = (
    <Menu>
      <Menu.Item>
        <a>Action</a>
      </Menu.Item>
      <Menu.Item>
        <a>Another action</a>
      </Menu.Item>
      <Menu.Item>
        <a>Something else here</a>
      </Menu.Item>
      <div className="dropdown-divider" />
      <Menu.Item>
        <a>Separated link</a>
      </Menu.Item>
    </Menu>
  )

  return (
    <div className={`card bg-light w-100 ${style.panel}`}>
      <div
        className={`card-header card-header-flex border-bottom-0 align-items-start ${style['card-header']}`}
      >
        <div
          className={`d-flex flex-column justify-content-center align-items-start ${style['card-title']}`}
        >
          <h3 className="font-weight-bold text-dark font-size-14 mb-0">
            {title} ({items.length})
          </h3>
        </div>
        <div className={`ml-auto d-flex flex-column justify-content-center ${style['card-menu']}`}>
          <div className="dropdown d-inline-block">
            <Dropdown overlay={dropdownMenu} placement="bottomRight">
              <button
                type="button"
                className="btn btn-light dropdown-toggle dropdown-toggle-noarrow"
              >
                <i className="fe fe-more-horizontal" />
              </button>
            </Dropdown>
          </div>
        </div>
      </div>
      <div className={`d-flex flex-column justify-content-between ${style['card-inner']}`}>
        <PerfectScrollbar>
          {items.map(item => item)}
          {/* <ReactSortable group="issues" list={items} setList={setItems} animation={300}>
            {items.map(item => item)}
          </ReactSortable> */}
        </PerfectScrollbar>
      </div>
      <div className={`card-footer ${style['card-footer']}`}>
        <Button className={style['load-more']} type="text" block>
          <i className="fe fe-plus d-inline-block mr-2" />
          Add New Card
        </Button>
      </div>
    </div>
  )
}

const Create = () => {
  return (
    <div className={`card bg-light w-100 ${style['panel-create']}`}>
      <div
        className={`card-header card-header-flex border-bottom-0 align-items-start ${style['card-header-create']}`}
      >
        <Button
          className={style['button-create']}
          icon={
            <i
              className="fe fe-plus"
              style={{
                fontSize: 16,
                fontWeight: 700,
                position: 'relative',
                top: 3,
                marginRight: 5,
              }}
            />
          }
          type="link"
        >
          Create New Kanban
        </Button>
      </div>
    </div>
  )
}

WorkspacePanel.Create = Create

export default WorkspacePanel
