import React, { useState, useRef } from 'react'
import ReactMarkdown from 'react-markdown'
import gfm from 'remark-gfm'
import { Button, Col, Input, Row } from 'antd'
import style from './style.module.scss'

const { TextArea } = Input

const TextEditor = props => {
  const {
    toolbar,
    locale = {
      submitText: 'Save',
    },
    placeholder = 'Add more detailed description...',
    children,
    defaultValue = children,
    markdown = false,
    style: displayStyle = {},
    ...restprops
  } = props

  const inputRef = useRef()
  const [editing, setEditing] = useState(false)
  const [value, setValue] = useState(defaultValue)

  // const handleInputBlur = () => {
  //   setEditing(false)
  // }

  const handleInputChange = e => {
    setValue(e.target.value)
  }

  const handleDisplayClick = () => {
    setEditing(true)
  }

  const handleCancelAction = () => {
    setEditing(false)
  }

  const handelSaveAction = () => {
    setEditing(false)
  }

  const renderValue = () => {
    if (value === undefined || value.trim() === '') {
      return placeholder
    }
    return value
  }

  return (
    <div className={style['text-editor']}>
      {editing ? (
        <React.Fragment>
          <TextArea
            ref={inputRef}
            value={value}
            placeholder={placeholder}
            style={displayStyle}
            onChange={handleInputChange}
            autoFocus
            {...restprops}
          />
          <Row className="my-2">
            <Col lg={12} className="toolbar">
              {toolbar}
            </Col>
            <Col lg={12} className="actions text-right">
              <Button onClick={handleCancelAction} type="text">
                Cancel
              </Button>
              <Button onClick={handelSaveAction} type="primary">
                {locale.submitText}
              </Button>
            </Col>
          </Row>
        </React.Fragment>
      ) : (
        <div className="display" style={displayStyle} onClick={handleDisplayClick} aria-hidden>
          {markdown ? (
            <ReactMarkdown plugins={[gfm]}>{renderValue()}</ReactMarkdown>
          ) : (
            renderValue()
          )}
        </div>
      )}
    </div>
  )
}

export default TextEditor
